@font-face {
  font-weight: 400;
  font-family: "Open Sans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("/fonts/opensans.woff") format("woff");
}
@font-face {
  font-weight: 700;
  font-family: "Open Sans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("/fonts/opensansbold.woff") format("woff");
}
.main-title {
  color: #000000;
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
  margin: 0;
}
@media (min-width: 768px) {
  .main-title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .main-title {
    font-size: 53px;
    line-height: 57px;
  }
}
.container {
  min-width: 260px;
  width: auto;
  padding: 0 30px;
  margin: 0;
}
.container--main-nav {
  display: flex;
  align-items: center;
}
.container--special-offer {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  padding: 24px 30px 40px 30px;
}
.container--special-offer::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 7px;
  background-image: url("../img/bg-zigzag-line.svg");
  background-repeat: repeat-x;
  transform: rotate(180deg);
  background-size: 320px 100%;
}
.container--promo {
  width: auto;
  min-width: 320px;
  padding: 0;
}
.container--contacts-button {
  width: 100vw;
}
.container--advantages {
  padding-top: 28px;
}
.container--reviews {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  position: relative;
}
.container--reviews::after {
  content: "";
  position: absolute;
  bottom: -50px;
  left: 0;
  width: 100%;
  height: 7px;
  background-image: url("../img/bg-zigzag-line.svg");
  background-repeat: repeat-x;
  transform: rotate(180deg);
  background-size: 320px 100%;
}
.container--page-footer {
  display: flex;
  flex-wrap: wrap;
}
.container--process {
  padding: 0 32px 0 34px;
  min-width: 190px;
  width: auto;
}
.container--modal-cart {
  display: flex;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
  }
  .container--special-offer {
    padding: 36px 50px 60px 50px;
    flex-direction: row;
    justify-content: space-between;
  }
  .container--special-offer::after {
    bottom: -5px;
    height: 17px;
    width: 100%;
    transform: rotate(180deg);
    background-size: 768px 100%;
  }
  .container--advantages {
    padding-top: 54px;
  }
  .container--reviews {
    width: auto;
    margin: 0;
    background-color: #ffffff;
    background-image: url("../img/review@1x.jpg");
    background-size: 350px 457px;
    background-repeat: no-repeat;
    padding: 54px 50px 20px 50px;
  }
  .container--reviews .container--page-footer {
    justify-content: space-between;
  }
  .container--reviews::after {
    transform: rotate(180deg);
    background-size: 768px 100%;
    height: 17px;
    width: 100%;
    bottom: -85px;
    left: 0;
  }
  .container--process {
    padding: 0 40px;
  }
  .container--modal-cart {
    width: 354px;
    padding: 34px 108px;
  }
}
@media (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
  .container--reviews {
    background-image: url("../img/review@2x.jpg");
  }
}
@media (min-width: 1150px) {
  .container {
    width: 1050px;
    padding: 0 50px;
    margin: 0 auto;
  }
  .container--special-offer {
    padding: 44px 50px 84px 50px;
    justify-content: flex-start;
  }
  .container--special-offer::after {
    bottom: -10px;
    left: 50px;
    height: 23px;
    background-size: 1050px 100%;
    width: 1050px;
  }
  .container--advantages {
    width: 700px;
    padding: 66px 175px 16px 175px;
  }
  .container--reviews {
    width: 875px;
    padding: 50px 0 66px 175px;
    margin: 0 auto;
  }
  .container--reviews::after {
    background-size: 1050px 100%;
    width: 1050px;
    bottom: -90px;
    height: 23px;
  }
  .container--page-footer {
    justify-content: space-between;
  }
  .container--process {
    order: 1;
    margin: -280px 58px 0 760px;
    padding: 0;
    width: 227px;
  }
  .container--contacts-button {
    width: 350px;
    padding: 0;
    order: 2;
    margin: -108px 0 0 -58px;
  }
  .container--modal-cart {
