.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}
.main-nav__wrapper {
  border-bottom: 2px solid #ececec;
}
.main-nav__logo-img {
  display: block;
  pointer-events: none;
}
.main-nav__logo {
  margin: 22px auto;
  padding-left: 20px;
}
.main-nav__logo:hover .main-nav__logo-img {
  opacity: 0.6;
}
.main-nav__logo:active .main-nav__logo-img {
  opacity: 0.3;
}
.main-nav__toggle {
  height: 14px;
  width: 20px;
  font-size: 0;
  border: none;
  background-color: transparent;
  position: relative;
  cursor: pointer;
}
.main-nav__toggle::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  height: 14px;
  width: 20px;
  background-image: url("../img/icon-menu-open.svg");
}
.main-nav-item {
  flex-basis: 100%;
}
.main-nav__item-text {
  display: block;
  pointer-events: none;
}
.main-nav__link {
  display: block;
  padding: 25px 0 25px 76px;
  color: #000000;
  text-decoration: none;
  border-bottom: 2px solid #ececec;
}
.main-nav__link:hover {
  opacity: 0.6;
}
.main-nav__link:active {
  opacity: 0.3;
}
.main-nav__link--icon:hover {
  opacity: 0.6;
}
.main-nav__link--icon:hover .main-nav__item-icon {
  fill: #63d1bb;
}
.main-nav__link--icon:active .main-nav__item-icon,
.main-nav__link--icon:active .main-nav__item-text {
  opacity: 0.3;
}
.main-nav__item--search,
.main-nav__item--cart {
  position: relative;
}
.main-nav__item-icon--search {
  pointer-events: none;
  position: absolute;
  top: 29px;
  left: 30px;
  width: 18px;
  height: 18px;
  fill: #d3d2d2;
}
.main-nav__item-icon--cart {
  pointer-events: none;
  position: absolute;
  top: 29px;
  left: 30px;
  width: 21px;
  height: 18px;
  fill: #d3d2d2;
}
.main-nav__new,
.main-nav__sale,
.main-nav__delivery {
  display: none;
  cursor: pointer;
  text-decoration: none;
  color: #000000;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--opened .main-nav__list {
    display: block;
  }
  .main-nav--opened .main-nav__toggle::before {
    width: 21px;
    height: 20px;
    background-image: url("../img/icon-menu-close.svg");
  }
  .main-nav--nojs .main-nav__list {
    display: block;
  }
  .main-nav--nojs .main-nav__toggle::before {
    background-image: none;
  }
}
