.modal-cart {
  z-index: 100;
  position: fixed;
  color: #000000;
  background-color: #ffffff;
  width: 100%;
  top: 50px;
  left: 50%;
  transform: translateX(-50%);
  border: 1px solid #c7c7c7;
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
  display: none;
}
.modal-cart--show {
  display: block;
}
.modal-cart__title {
  width: 57%;
  font-size: 25px;
  line-height: 30px;
  text-transform: uppercase;
  text-align: center;
  margin: 30px auto 10px auto;
}
.modal-cart__text {
  color: #444444;
  width: 100%;
  font-size: 15px;
  line-height: 30px;
  text-align: center;
  margin-bottom: 18px;
}
.modal-cart__form {
  width: 100%;
}
.modal-cart__input-block {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin: 20px 0 44px 0;
}
.modal-cart__input-item {
  flex-basis: 30%;
}
.modal-cart__label {
  display: block;
  text-align: center;
  padding: 16px 0;
  width: 100%;
  font-size: 17px;
  line-height: 16px;
  font-weight: 700;
  border: 2px solid #ececec;
}
.modal-cart__label:hover {
  border: 2px solid #c7c7c7;
}
.modal-cart__input:checked ~ .modal-cart__label {
  border: 2px solid #63d1bb;
}
.modal-cart__submit {
  width: 100%;
  line-height: 16px;
  padding: 20px 0;
  background-color: #63d1bb;
  border: none;
